import React, { Component } from 'react';
import ReactSVG from "react-svg";
import { Text } from "../";
import Link from "next/link";

class Messenger extends Component {
  constructor(props) {
    super(props);
    this.state = {
      icon: '/static/img/icon/lync-box.svg',
      title: 'Lync',
      description: <React.Fragment><span>Automate your internal processes to make your employees spend more time on their work.</span><span className="mt-16">With Botgate that process can feel like a real person, and your bot can use quick actions to save more time.</span></React.Fragment>,
      question: 'What are Lync features?',
      featureList: ['It supports customized rich messages (images, links etc.)', 'It is another fast & easy way to contact your customers', 'It keeps track of the conversation history'],
      image: null,
      subDescription: 'All you need to do is attach your bot to your facebook page on the <b>channels tab.</b> You can attach the same bot to multiple channels.',

    }
  }


  render() {
    let { icon, title, description, question, featureList, image, subDescription, subDescription2 } = this.state
    return (
      <div className="container">
        <div className="text-center">
          <ReactSVG className="svg" src={icon} />
          <Text type="h2" text={[title]} />
          <p className="text-16 mt-16 text">
            {description}
          </p>
          <p className="text-thick text-16 mt-16 text">
            {question}
          </p>
          <div className="text-16 mt-16 text">
            <ul className="m-0 ul p-0">
              {featureList.map((feature, index) =>
                <li key={index}>
                  {feature}
                </li>
              )}
            </ul>
          </div>
          {
            image &&
            < img className="content-image" src={image} />
          }
          <p className="text mt-96" dangerouslySetInnerHTML={{ __html: subDescription }} />
          <p className="text mb-64"> Click <Link href="/integrations"><a>here</a></Link> to see the other integrations. </p>
        </div>
      </div>
    );
  };
};

export default Messenger;